.about {
	display: flex;
	flex-direction: column;
	justify-content: center;
	width: 100%;
	height: auto;
	margin-top: 50px;
	padding: 20px;
	background-color: white;
	font-weight: 300;
	color: #adaeb9;
	border-radius: 5px;
	box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
		0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.about__title {
	margin-bottom: 5px;
	font-size: 24px;
	color: #2c2c2c;
}

.about__description {
	font-size: 20px;
}
